<template>
  <el-dialog :visible.sync="dialogData.dialogVisible" id="register_dialog">
    <div class="all">
      <div class="header">
        <!-- <span>注册账号</span> -->
      </div>

      <div class="m-form">

        <div>
          <label for="telephone"></label>
          <input id="telephone" placeholder="请输入6-12位帐户名" name="telephone" type="txt" v-model="telephone"
                 class="u-txt" required pattern="^[a-zA-Z0-9_-||\u4e00-\u9fa5]{6,12}$"><br/>
        </div>

        <div>
          <label for="address"></label>
          <input id="verification" placeholder="请输入Nas钱包地址" name="address" type="txt" class="u-txt" v-model="address"><br/>

        </div>
        <div>
          <label for="password"></label>
          <input id="password" placeholder="请输入密码" name="password" type="password" class="u-txt" v-model="password"><br/>

        </div>
        <div>
          <label for="invitation"></label>
          <input id="invitation" placeholder="请输入邀请码" name="invitation" type="txt" class="u-txt" v-model="invitation"><br/>

        </div>

        <div>
          <button class="register" name="loginBtn" @click="register">注 册</button>
        </div>
      </div>
    </div>

  </el-dialog>
</template>

<script>
  import axios from 'axios'

  export default {
    props: ['dialogData'],
    data() {
      return {
        telephone: '',
        address: '',
        password: '',
        invitation: '',
      };
    },
    methods: {
      register() {
        let sendData = {
          nickname: this.telephone,
          password: this.password,
          address: this.address,
          inviteCode: this.invitation
        }
        let reg = /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){7,15}|1[0-9]{10}$/;
        let areg = reg.test(this.telephone);
        let areg2 = reg.test(this.password);
        if (areg == false) {
          alert("请输入正确的用户名");
        } else if (areg2 == false) {
          alert("密码长度不能少于8位");
        } else {
          axios.post('http://192.168.1.212:9000/register', sendData)
            .then((res) => {
              if (res.data.code == 0) {
                // window.location.href = 'zerg.oowan.com'
                alert("注册成功")
              } else if (res.data.code == 1) {
                alert("此地址已注册")
              } else {
                alert(res.data.msg)
              }
            })
            .catch(function (error) {
            });
        }

      }

    },
    mounted() {
    }
  };
</script>

<style lang="less" rel="stylesheet/less">
  .el-dialog {
    width: 350px;
    background-color: rgba(0, 0, 0, 0);
  }

  #register_dialog {
    .el-dialog__header {
      padding: 0;
    }
    .el-dialog__body {
      border: 1px solid #eee;
      border-radius: 10px;
      border-color: rgba(255, 255, 255, 0.4);
      background-color: rgba(0, 0, 0, 0.6);
      padding: 0;
    }
  }


</style>
<style lang="less" rel="stylesheet/less" scoped>
  .m-form input {
    padding-left: 10px;
  }

  .all {
    width: 350px;
    height: 500px;
    background-color: rgba(32, 42, 56, 0.3);
    text-align: center;
    border-radius: 6px;
    margin: 0 auto;
  }

  .header {
    background-image: url('../assets/img/zca.png');
    background-repeat: no-repeat;
    background-size: 160px 35px;
    background-position: center;
    width: 100%;
    height: 80px;
    text-align: center;
    line-height: 80px;
    color: white;
    font-size: 24px;
  }

  .header span {
    letter-spacing: 16px;
    color: #0d131f;
  }

  .m-form {
    width: 100%;
    /*	height:8.47rem;*/
    /*background-color: pink; */
  }

  .m-form input {
    border: none;
    outline: medium;
    width: 90%;
    height: 60px

  }

  .u-txt {
    height: 60px;
    width: 300px;
    border-radius: 5px;
    margin-bottom: 15px;
  }

  .m-form button {
    width: 300px;
    height: 50px;
    color: white;
    /*border: 1px solid #ddd;*/
    /*cursor: pointer;*/
    background-color: #192332;
    margin-top: 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .m-form button:active {
    background-color: gray;
  }
</style>
<style>
  @media screen and (orientation: portrait) {
    .el-dialog {
      width: 100% !important;
    }
  }
</style>



